import React,{useEffect,useState}  from 'react'
import styled from 'styled-components';
import './page.css';
import './slider.css';
import logo1 from './../img/logoff.png';
import l1 from './../img/l1.jpg';
import l2 from './../img/l2.jpg';
import l3 from './../img/l3.jpg';
import {Link as Link2} from 'react-router-dom';
import List1 from './List1';
import {Link as Link1} from 'react-scroll';
import Slider from 'react-slick';
import img1 from "./../img/img2.png";
import img2 from "./../img/img3.png";
import hung from "./../img/hung2.png";
import nghia from "./../img/nghia.png";
import minh from "./../img/minh.png";
import SearchIcon from '@mui/icons-material/Search';
import "./list.css";
import fireDb from "../firebase";
import { useHistory } from 'react-router-dom';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import HomeIcon from '@mui/icons-material/Home';
import ScrollToTop from 'react-router-scroll-top';
const Container = styled.div`
    height: 80px;
    background-color:#2C6C70;
    position: fixed;
    top:0;
    z-index: 10;
    width:100%;
   `
const Wrapper = styled.div`

 display:flex;
 
`

const Home =styled.div`
font-family: sans-serif;
font-size:13px;
color:white;
 letter-spacing:0.15em;
 padding: 30px 30px 20px 30px;
 font-weight: bold;
`
const About = styled.div`
font-weight: bold;
font-family: sans-serif;
font-size:13px;
color:white;
 letter-spacing:0.15em;
 padding: 30px 30px 20px 30px;
`
const List = styled.div`
font-weight: bold;
font-family: sans-serif;
font-size:13px;
color:white;
 letter-spacing:0.15em;
 padding: 30px 30px 20px 30px;
`
const Logo = styled.div`
width:400px;
color:white;
`
const Search = styled.div`
border: 0.5px solid white;;
display:flex;
width: 250px;
height:25px;
color:white;
margin-top: 25px;
margin-left:60px;


`

const Main = styled.div`

`
const Container1 = styled.div`
   margin-left: 32px;                                           
`



const Navbar = () => {
    var settings = {
        dots: true,
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 1800,
        
      };
      const [data,setData] = useState({});
      const [search,setSearch]=useState("");
      
      const history =useHistory();
      
  useEffect(()=>{
    fireDb.child(`junglewildlifemagazine`).on("value",(snapshot)=>{
      if(snapshot.val() !== null ){
        setData({...snapshot.val() });
      }else{
        setData({});
      }
     
    });
    return () =>{
      setData({});
    };
  }, []);

const handleSubmit = (e) =>{
  e.preventDefault();
  history.push(`/search?tenDiaphuong=${search}`);
  setSearch("");
};


    return (
        <Main>
       <Container>
           <Wrapper>
           <Logo className='Logo'>
              <img src={logo1}></img>
              </Logo>
              <Home className='Home'>
                <Link1 activeClass="active" className="test1" to="slider" spy={true} smooth={true} duration={500} > HOME </Link1>
              </Home>
              <List className='List'>
                <Link1 activeClass="active" className="test1" to="card-body" spy={true} smooth={true} duration={500} >  DANH SCH </Link1>
              </List >
              <About className='About'>
              <Link1 activeClass="active" className="test1" to="Lienhe" spy={true} smooth={true} duration={800} >  LIN H </Link1>
              </About>
              
              
              <Search>
                <form onSubmit={handleSubmit}>
                <input className='input' placeholder='TM KIM' 
                type="text" 
                onChange={(e)=>setSearch(e.target.value)}
                value={search} 
               

                />
                </form>
                <i class="fas fa-search"></i>
              </Search>
              
           </Wrapper>
       </Container>
       <div className='slider'>
        <div className='content'>
          <h3>V chng ti</h3>
          <h1>Jungle Wildlife Magazine</h1>
          <p>L mt tp ch trc tuyn trng by hnh nh v thng tin tt c cc ng vt ca rng nhit i trong khu vc ng bng Sng Cu Long cng nh trong h sinh thi ng vt sinh sng trong khu vc chu th sng Mekong. Chng ti mang s mnh truyn ti thng ip v s s chia chung tay bo v ngun a dng sinh hc v thc y qu trnh bo tn ng thc vt qu him trn th gii</p>
        </div>
           <div className='slideshow'>
      
        <Slider {...settings}>
          <div className='slideshow1'>
            <h3 className='imgslide'><img src={l1}></img></h3>
          </div>
          <div>
          <h3 className='imgslide'>  <img src={l2}></img></h3>
          </div>
          <div>
          <h3 className='imgslide'>  <img src={l3}></img></h3>
          </div>
         
        </Slider>
      </div>
      </div>
        <div className='title1'>
        
                  <h2>  DANH SCH NG VT </h2>
          
       </div>
          <Container1>
        {Object.keys(data).map((id) =>{
          return(
            <div className="card" key={id}>
            <div className="card-body">
              <img src={data[id].img1}></img>
              <h3>{data[id].tenDiaphuong}</h3>
              <Link1 activeClass="active" className="test1" to="logo" spy={true} smooth={true} duration={800} > <Link2 to={`/view/${id}`} ><button className="card-btn" ><SearchIcon /></button></Link2> </Link1>
                 
             
  
            </div>
          </div>
       
          )
        })}
          
      </Container1>
      
      <div className='Lienhe'>
     
          <div className='thanhvien'>
            <div className='avt'>
                <img src={hung}></img>
            </div>
            <div className='tenThanhvien'>
            <span>Nguyn Bi Hng</span>
            </div>
            <div className='tughi'>
            <span>Ch tch hip hi ng vt hoang d<br/> Vit Nam.</span>
            </div>
            <div className='kinhnghiem'>
              <span>ng Nguyn Bi Hng, ngi sng lp v l Ch tch hip hi ng vt hoang d Vit Nam, ng sng lp Jungle Wildlife Magazine. ng Hng c hn 20 nm kinh nghim trong lnh vc pht trin a dng sinh hc v c kin thc nghip v chuyn su ngnh ti chnh v qun tr doanh nghip.</span>
            </div>
          </div>

          <div className='thanhvien'>
            <div className='avt'>
                <img src={nghia}></img>
            </div>
            <div className='tenThanhvien'>
                <span>Trn Nhn Ngha</span>

            </div>
            <div className='tughi'>
                <span>Quyn gim c T chc Quc t v Bo tn Thin nhin ti Vit Nam (WWF). </span>
            </div>
            <div className='kinhnghiem'>
              <span>C 10 nm kinh nghim qun l ngun nhn lc, 5 nm lm vic vi cc t chc ton cu th gii v 18 nm kinh nghim trong lnh vc Cng ngh thng tin. Vi kin thc chuyn su v ngun nhn lc trong lnh vc thin nhin v mi trng, ng Ngha hin ang m nhn vai tr qun l ngun nhn lc lm vic ti T chc Quc t v Bo tn Thin nhin ti Vit Nam (WWF Vietnam)</span>
            </div>
          </div>


          <div className='thanhvien'>
            <div className='avt'>
                <img src={minh}></img>
            </div>
            <div className='tenThanhvien'>
                <span>Phm Hong Minh</span>
            </div>
            <div className='tughi'>
              <span>Phng vin T chc Bo tn thin nhin ti ng Nam  (The Nature Conservancy).</span>
            </div>
            <div className='kinhnghiem'>
              <span>Phng vin Hong Minh  ng sng lp Jungle Wildlife Magazine. ng  c 30 nm lm vic trong lnh vc bo ch v lm vic ti 10 quc gia ng Nam . ng l ng tc gi ca cun sch Nhng con vn mong manh do Nothing xut bn vo nm 20xx.</span>
            </div>
          </div>


      </div>
        <div className="contact">
          <div className="logocontact">
          <p>Jungle Wildlife Magazine</p>
          </div>
          <div className='diachi'>
          <h3>a ch</h3>
         <div className='address'> <HomeIcon/><span>ng 3/2, Xun Khnh, Ninh Kiu, Cn Th</span></div><br/>
         <div className='address'> <LocalPhoneIcon/><span>0344109778</span></div>
         </div>
       
        </div>

       </Main>
    )
}





export default Navbar
